.navbar.active {
  background: linear-gradient(
    90deg,
    rgba(108, 2, 255, 1) 41%,
    rgba(159, 2, 255, 1) 81%
  );
  color: #fff;
}

.navbarBrand img {
  width: 50px;
  margin: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-right: -50%;
  transform: translate(-55%, -50%);
}

@keyframes Rotate {
  0% {
    transform: rotateY(0);
  }
  10% {
    transform: rotateY(36deg);
  }
  20% {
    transform: rotateY(72deg);
  }
  30% {
    transform: rotateY(108deg);
  }
  40% {
    transform: rotateY(144deg);
  }
  50% {
    transform: rotateY(180deg);
  }
  60% {
    transform: rotateY(216deg);
  }
  70% {
    transform: rotateY(252deg);
  }
  80% {
    transform: rotateY(288deg);
  }
  90% {
    transform: rotateY(324deg);
  }
  100% {
    transform: rotateY(360deg);
  }
}

.navbarBrand {
  background-color: white;
  height: 60px;
  width: 60px;
  border-radius: 110px;
  position: relative;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.2),
    0 1px 5px 0 rgba(0, 0, 0, 0.12);
}
.navbarBrand.rotate {
  animation: Rotate;
  animation-duration: 1s;
  animation-timing-function: linear;
}

#navbar-tlse-home {
  background-color: transparent;
  height: 70px;
  top: 0;
  position: fixed;
  width: 100%;
  z-index: 1000;
}

#navbar-tlse {
  background: linear-gradient(
    90deg,
    rgba(108, 2, 255, 1) 41%,
    rgba(159, 2, 255, 1) 81%
  );
  color: #fff;
  height: 70px;
  top: 0;
  position: fixed;
  width: 100%;
  z-index: 1000;
}

.navbar {
  .nav-item a {
    color: white;
    font-family: Rubik;
  }
}

.nav-link.active a {
  color: #fff;
}

.navbar-light .navbar-nav .nav-link {
  color: white;
}

.login-button {
  cursor: pointer;
}

.nav-usermenu {
  margin-left: auto;
}

.username {
  color: white;
  font-family: Rubik;
  font-weight: 400;
  font-size: 1rem;
  line-height: 1.5;
  position: absolute;
  margin: 0;
  position: relative;
  top: 50%;
  transform: translate(0, -50%);
}

.logout-btn {
  font-family: Hack, monospace;
  background: #fff;
  color: #2c2c2c;
  cursor: pointer;
  font-size: 16px;
  padding: 1rem;
  border: 0;
  transition: all 0.5s;
  border-radius: 10px;
  width: auto;
  position: relative;
  min-width: 120px;

  &::after {
    content: url("../logos/sign-out.svg");
    font-family: "Font Awesome 5 Pro";
    font-weight: 400;
    position: absolute;
    left: 80%;
    top: 54%;
    right: 0;
    bottom: 0;
    opacity: 0;
    transform: translate(-50%, -50%);
  }

  &:hover {
    background: #2b2bff;
    transition: all 0.5s;
    border-radius: 10px;
    box-shadow: 0px 6px 15px #0000ff61;
    padding: 1rem 2.2rem 1rem 1rem;
    color: #ffffff;
    &::after {
      opacity: 1;
      transition: all 0.5s;
      color: #ffffff;
    }
  }
}
