.linkedin {
  background-color: $ourblue;
  border-radius: 50%;
  color: white;
  transition: 400ms linear;
  :hover {
    background-color: #f15bb5;
    border-radius: 50%;
  }
  svg {
    fill: #fff;
    height: 40px;
    width: 40px;
    transition: 400ms linear;
  }
  svg:hover {
    fill: #f7f7f7;
  }
}

.carousel-inner {
  border-bottom-left-radius: 50% 30%;
}

.github {
  background-color: $ourblue;
  border-radius: 50%;
  margin-right: 5px;
  transition: 400ms linear;
  :hover {
    background-color: #f15bb5;
    border-radius: 50%;
  }
  svg {
    fill: #fff;
    height: 40px;
    width: 40px;
    transition: 400ms linear;
  }
  svg:hover {
    fill: #f7f7f7;
  }
}

