.comments-row {
  display: flex;
}
#contact-form {
  margin-top: 100px;
  display: flex;
  flex-direction: row;
  padding: 0;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.2),
    0 1px 5px 0 rgba(0, 0, 0, 0.12);
  border-radius: 15px;
}

.message-sent {
  display: none;
}

#contact-form-content {
  background-color: #e7ecef;
  padding: 0;
  border-top-right-radius: 15px;
  border-bottom-right-radius: 15px;
  h1 {
    font-size: 30px;
  }
}

.contactus-pic-col {
  width: 100%;
  background-color: #404e4d;
  border-top-left-radius: 15px;
  border-bottom-left-radius: 15px;
  img {
    width: 100%;
    margin: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    margin-right: -50%;
    transform: translate(-50%, -50%);
  }
}

.form-container {
  height: 350px;
  transition: all 0.6s ease-in-out;
  border-radius: 10px;
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
  margin: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-right: -50%;
  transform: translate(-50%, -75%);
  padding: 20px;
}

.sign-in-container {
  width: 350px;
  z-index: 2;
  margin-top: 20px;
  h1 {
    font-size: 2em;
    font-weight: bold;
    font-family: monty;
  }
  span {
    font-size: 12px;
    font-family: monty;
  }
}

.forgot-password {
  color: #333;
  font-size: 14px;
  text-decoration: none;
  font-family: monty;
  a {
    color: #333;
  }
}

.social-container {
  margin: 20px 0;
  a {
    border: 1px solid #dddddd;
    border-radius: 50%;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    margin: 0 5px;
    height: 40px;
    width: 40px;
  }
  svg {
    width: 14px;
    height: 14px;
  }
}

.login-submit-button {
  border-radius: 20px;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.2),
    0 1px 5px 0 rgba(0, 0, 0, 0.12);
  background: linear-gradient(
    90deg,
    rgba(108, 2, 255, 1) 41%,
    rgba(159, 2, 255, 1) 81%
  );
  color: #ffffff;
  font-size: 12px;
  font-weight: bold;
  padding: 12px 45px;
  letter-spacing: 1px;
  text-transform: uppercase;
  transition: transform 80ms ease-in;
}

.submit-button {
  background-color: white;
  padding: 15px;
  margin-left: 0;
  margin-right: 0;
  border-bottom-right-radius: 15px;
  button {
    border-radius: 19px;
    padding: 10px 25px;
    background-color: $primarycolor;
  }
}
.contactus-form-core {
  padding: 15px;
  height: 405px;
}

.form-group {
  height: 70px;
  label {
    width: 100%;
    text-align: left;
  }
  input {
    float: left;
    border: 1px solid rgba(0, 0, 0, 0.1);
    border-radius: 4px;
    padding: 5px;
    width: 100%;
  }
  textarea {
    float: left;
    width: 91%;
    border: 1px solid rgba(0, 0, 0, 0.1);
    border-radius: 4px;
    padding: 5px;
  }
}

#message {
  height: 140px;
  position: absolute;
  width: 100%;
}

#message-sent,
#post-added {
  display: none;
}

.small-photo {
  background-color: white;
  border: solid 0.5px lightgrey;
  padding: 7px;
  margin: 0 10px;
  z-index: 1;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.2),
    0 1px 5px 0 rgba(0, 0, 0, 0.12);
  width: 33%;
  img {
    width: 100%;
    height: 200px;
    object-fit: cover;
    filter: grayscale(100%);
  }
}

.photo-row {
  display: flex;
}

.user-image {
