  img {
    width: 50px;
    object-fit: cover;
    position: absolute;
    top: 50%;
    left: 50%;
    margin-right: -50%;
    transform: translate(-55%, -50%);
  }
}

.comment_created_date {
  text-align: right;
}

#comments {
